'use client';
import React, { useState } from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';
import VentaDetalleModal from './VentaDetalleModal';
import VentaEditarModal from './VentaEditarModal';
import VentasFiltro from './VentasFiltro';
import { VentaServicio } from '../../../entities/venta';

const VentasTable = () => {
  const { ventas, eliminarVenta, editarVenta } = useVentas();
  const [ventaSeleccionada, setVentaSeleccionada] = useState<VentaServicio | null>(null);
  const [editando, setEditando] = useState<VentaServicio | null>(null);

  const filtrosIniciales = {
    cliente: '',
    fechaInicio: '',
    fechaFin: '',
  };

  const [filtros, setFiltros] = useState(filtrosIniciales);

  const limpiarFiltros = () => {
    setFiltros(filtrosIniciales);
  };

  const ventasFiltradas = ventas.filter((v) => {
    const nombreCliente = v.cliente.nombre.toLowerCase();
    const clienteOK = nombreCliente.includes(filtros.cliente.toLowerCase());

    const fechaVenta = new Date(v.fechaRegistro).getTime();
    const desde = filtros.fechaInicio ? new Date(filtros.fechaInicio).getTime() : null;
    const hasta = filtros.fechaFin ? new Date(filtros.fechaFin).getTime() : null;
    const fechaOK = (!desde || fechaVenta >= desde) && (!hasta || fechaVenta <= hasta);

    return clienteOK && fechaOK;
  });

  const handleDelete = (venta: VentaServicio) => {
    const confirm = window.confirm(`¬øDeseas eliminar la venta ${venta.idVentaServicio}?`);
    if (confirm) {
      eliminarVenta(venta.idVentaServicio);
    }
  };

  const exportarExcel = async () => {
    const XLSX = await import('xlsx');

    const data = ventasFiltradas.map((venta) => ({
      ID: venta.idVentaServicio,
      Cliente: venta.cliente.nombre,
      Correo: venta.cliente.correo,
      Tel√©fono: venta.cliente.telefono,
      Fecha: formatDate(venta.fechaRegistro),
      Servicios: venta.detalles.length,
      Total: venta.costoTotal,
      Descuento: venta.descuento,
    }));

    const workbook = XLSX.utils.book_new();
    const worksheet = XLSX.utils.json_to_sheet(data);
    XLSX.utils.book_append_sheet(workbook, worksheet, 'Ventas');
    XLSX.writeFile(workbook, 'ventas.xlsx');
  };

  return (
    <>
      <VentasFiltro filtros={filtros} onFiltroChange={setFiltros} />

      <div className="flex justify-end mb-3 gap-3">
        <button
          onClick={limpiarFiltros}
          className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-md transition"
        >
          üßπ Limpiar filtros
        </button>
        <button
          onClick={exportarExcel}
          className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md transition"
        >
          üì§ Exportar a Excel
        </button>
      </div>

      <div className="table-container overflow-auto">
        <table className="w-full table-auto border-collapse">
          <thead>
            <tr className="bg-gray-100 text-left">
              <th className="px-4 py-2 border">ID Venta</th>
              <th className="px-4 py-2 border">Cliente</th>
              <th className="px-4 py-2 border">Correo</th>
              <th className="px-4 py-2 border">Tel√©fono</th>
              <th className="px-4 py-2 border">Fecha</th>
              <th className="px-4 py-2 border">Servicios</th>
              <th className="px-4 py-2 border">Total</th>
              <th className="px-4 py-2 border">Acciones</th>
            </tr>
          </thead>
          <tbody>
            {ventasFiltradas.map((venta) => (
              <tr key={venta.idVentaServicio} className="hover:bg-gray-50">
                <td className="px-4 py-2 border">{venta.idVentaServicio}</td>
                <td className="px-4 py-2 border">{venta.cliente.nombre}</td>
                <td className="px-4 py-2 border">{venta.cliente.correo}</td>
                <td className="px-4 py-2 border">{venta.cliente.telefono}</td>
                <td className="px-4 py-2 border">{formatDate(venta.fechaRegistro)}</td>
                <td className="px-4 py-2 border text-center">
                  <span className="inline-block bg-blue-100 text-blue-700 px-2 py-1 text-xs rounded-full">
                    {venta.detalles.length}
                  </span>
                </td>
                <td className="px-4 py-2 border">{formatCurrency(venta.costoTotal)}</td>
                <td className="px-4 py-2 border">
                  <div className="flex gap-2">
                    <button
                      title="Ver"
                      onClick={() => setVentaSeleccionada(venta)}
                      className="text-indigo-600 hover:text-indigo-800 text-sm font-medium"
                    >
                      üëÅÔ∏è
                    </button>
                    <button
                      title="Editar"
                      onClick={() => setEditando(venta)}
                      className="text-yellow-600 hover:text-yellow-800 text-sm font-medium"
                    >
                      ‚úèÔ∏è
                    </button>
                    <button
                      title="Eliminar"
                      onClick={() => handleDelete(venta)}
                      className="text-red-600 hover:text-red-800 text-sm font-medium"
                    >
                      üóëÔ∏è
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {ventasFiltradas.length === 0 && (
          <div className="text-center text-gray-500 py-6">
            No se encontraron resultados con los filtros aplicados.
          </div>
        )}
      </div>

      {ventaSeleccionada && (
        <VentaDetalleModal
          venta={ventaSeleccionada}
          onClose={() => setVentaSeleccionada(null)}
        />
      )}

      {editando && (
        <VentaEditarModal
          venta={editando}
          onClose={() => setEditando(null)}
          onGuardar={(ventaActualizada) => {
            editarVenta(ventaActualizada);
            setEditando(null);
          }}
        />
      )}
    </>
  );
};

export default VentasTable;
